/* eslint-disable */
import { createStore } from 'vuex'
import createPersistedState from 'vuex-persistedstate';

const store = createStore({
  state() {
    return {
      userData: {}, // готов
      userChannels: [],
    };
  },
  mutations: {
    setUserData(state, data) {
      state.userData = data
    },
    setUserChannels(state, data) {
      state.userChannels = data
    }
  },
  actions: {
    asyncSetUserData(context, data) { // ненадо
      setTimeout(() => {
        context.commit('setUserData', data);
      }, 1000);
    },
  },
  getters: {
    getUserData: (state) => state.userData,
    getUserChannels: (state) => state.userChannels,
  },
});

const persistedStateOptions = {
  key: 'my-vuex-state', // ключ для сохранения в localStorage
  storage: window.localStorage, // можно использовать window.sessionStorage
};

createPersistedState(persistedStateOptions)(store);

export default store;
